import React, { useEffect, useState } from "react";
import InputElement from "../../formElements/InputElement";
import { Form, Formik } from "formik";
import ButtonElement from "../../formElements/ButtonElement";
import { tableGroupSchema } from "../../../util/Schema";
import {
  action,
  code,
  createGroup,
  groups,
  status,
  updateGroup,
} from "../../../redux/slices/GroupSlice";
import { useSelector, useDispatch } from "react-redux";
import toast, { Toaster } from "react-hot-toast";
import ListIcon from "../../formElements/ListIcon";
import { useLocation, useNavigate } from "react-router-dom";
import SelectElement from "../../formElements/SelectElement";
import LineSpinner from "../../customLoader/LineSpinner";

const TableGroup = () => {
  const [initial, setInitial] = useState({ Groupname: "", inactive: "" });
  const [edit, setEdit] = useState(false);
  const Status = useSelector(status);
  const Code = useSelector(code);
  const Groups = useSelector(groups);
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const location = useLocation();

  useEffect(() => {
    if (Code === 201 || Code === 200) {
      toast.success(Status);
    } else if (Code === 500 || Code === 404 || Code === 403) {
      toast.error(Status);
    }

    return () => {
      dispatch(action.cleanup({ type: "add" }));
    };
  }, [Code, Status]);

  useEffect(() => {
    if (location.state?.data !== undefined && Groups.length > 0) {
      const identifiedGroup = Groups.filter(
        (grp) => grp.id === location.state?.data
      );
      const initialState = {
        Groupname: identifiedGroup[0].Groupname,
        inactive: identifiedGroup[0].inactive,
        id: identifiedGroup[0].id,
      };
      setInitial(initialState);
      setEdit(true);
    }

    return () => {
      location.state = null;
    };
  }, [location.state]);

  const navigationHandler = () => {
    navigate("../TG/List");
  };

  return (
    <>
      {Status === "loading" && <LineSpinner />}
      <Toaster />
      <div className="px-20 py-8 overflow-auto flex  justify-center h-[calc(100vh-70px)]">
        <div className="  w-full md:w-1/3 px-4 ">
          <h1 className="text-center font-semibold text-3xl py-3">
            Table Group
          </h1>
          <div className="w-80 py-3">
            <div
              className="float-end  text-xl text-gray-700 font-semibold cursor-pointer hover:text-orange-500"
              onClick={navigationHandler}
            >
              <ListIcon />
            </div>
          </div>

          <Formik
            initialValues={initial}
            enableReinitialize={true}
            validationSchema={tableGroupSchema}
            onSubmit={(values, action) => {
              if (!edit) {
                dispatch(createGroup(values));
              } else {
                dispatch(updateGroup({ ...values }));
                setEdit(false);
              }

              action.setSubmitting(false);
              action.resetForm({
                values: {
                  Groupname: "",
                  inactive: "",
                },
              });
            }}
          >
            {(props) => {
              return (
                <Form>
                  <div className=" flex flex-col  justify-center items-center  text-center mt-5 gap-3">
                    <InputElement
                      type="text"
                      placeholder="Table Group "
                      name="Groupname"
                    />

                    <SelectElement name="inactive">
                      <option value="">Inactive ? </option>
                      <option value="0">No</option>
                      <option value="1">Yes</option>
                    </SelectElement>

                    <ButtonElement
                      type="submit"
                      caption="SAVE"
                      disabled={props.isSubmitting}
                    />
                  </div>
                </Form>
              );
            }}
          </Formik>
        </div>
      </div>
    </>
  );
};

export default TableGroup;
